import React from 'react';
import emailjs from 'emailjs-com';
import Logo from "../../image/Logo.png";
import "../../App.css"
import "../../css/QnA.css"
import { Link } from "react-router-dom";


export default function QnA() {

    function sendEmail(e){
        e.preventDefault();

        emailjs.sendForm(
            'service_kdh',
            'template_xw0dnqc',
            e.target,
            '_jS58pRqqhKudZIRC'
            ).then(res=>{
                console.log(res);
                alert('  .');
                window.location.reload();
            }).catch(err => console.log(err));
    }

    return(
        <div className='qnapage'>
        <div className='qna-content'>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
        <div class="firefly"></div>
            <Link to='/'>
                <div className='qna-logo'>
                    <img src={Logo} alt="PictoMaker-Logo" style={{width:"150px",height:"70px"}}/>
                </div>
            </Link>

            <form onSubmit={sendEmail}>
                <div className='qna-form'>
                    <div className='qna-left'>
                        <div className='left-input'>
                            <label></label><br/>
                            <input type="text" name="user_name" placeholder=" "/><br/>

                            <label></label><br/>
                            <input type="email" name="user_email" placeholder=" "/>
                        </div>
                        </div>

                        <div className='qna-right'>
                            <div className='right-input'>
                            <label></label><br/>
                            <textarea name='message' rows='50' className='textareat' placeholder=" ."/>
                        </div>
                    </div>
                    <input type='submit' value='' className='sendbutton'/>
                </div>
            </form>
        </div>
        </div>
    );
}